.content.rental__history__detail > .page__title__wrap {
  grid-column: span 2 / span 2;
}
.section__grid__wrap.rental__history__detail {
  grid-template-columns: repeat(2, minmax(0, 1fr));
}

.content.rental__history__detail .sub__nav {
  grid-column: span 2 / span 2;
}
.content.rental__history__detail .id__wrap .section__inner > input {
  /* width: 100%; */
  background: rgb(247, 247, 247);
}
.content.rental__history__detail .grid1 {
  grid-column: span 2 / span 2;
}
.content.rental__history__detail .user__certification .section__inner > .list__wrap {
  width: 100%;
}
.content.rental__history__detail .user__certification .section__inner ul {
  display: flex;
  justify-content: space-between;
}
.content.rental__history__detail .user__certification .section__inner ul > li {
  width: 32%;
}
.content.rental__history__detail .user__certification.station .section__inner ul > li {
  width: 48%;
}
.content.rental__history__detail .user__certification .section__inner ul dl {
  display: flex;
  align-items: center;
  width: 100%;
}
.content.rental__history__detail .user__certification .section__inner ul dl dt {
  margin-right: 16px;
  white-space: nowrap;
}
.content.rental__history__detail .user__certification .section__inner ul dl dd {
  width: 100%;
}
.content.rental__history__detail .user__certification .section__inner ul dl dd input {
  width: 100%;
}
.content.rental__history__detail .battery .info__wrap {
  display: flex;
  flex-direction: column;
}
.content.rental__history__detail .battery .info__wrap > dl {
  margin-bottom: 18px;
  display: flex;
  align-items: center;
}
.content.rental__history__detail .battery .info__wrap > dl > dt {
  margin-right: 16px;
}
.content.rental__history__detail .battery .info__wrap > div {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.content.rental__history__detail .battery .info__wrap > div ul > li {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 16px;
}
.content.rental__history__detail .battery .info__wrap > div ul > li > p {
  background: rgb(215, 215, 215);
  border-radius: 4px;
  width: 30px;
  height: 30px;
  line-height: 30px;
  text-align: center;
}
.content.rental__history__detail .battery .info__wrap > div ul > li:last-of-type {
  margin-bottom: 0px;
}
.content.rental__history__detail .battery .info__wrap > div > p {
  width: 112px;
  min-height: 112px;
  border-radius: 4px;
  margin-right: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 1.4rem;
}
.content.rental__history__detail .battery .info__wrap > div > p.suc {
  background: #376092;
}
.content.rental__history__detail .battery .info__wrap > div > p.fail {
  background: #fc3b3b;
}
.content.rental__history__detail .battery .info__wrap > div ul {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.content.rental__history__detail .battery .info__wrap > div ul li dl:first-of-type dd input {
  max-width: 70px;
}
.content.rental__history__detail .battery .info__wrap > div ul li dl:last-of-type dd input {
  max-width: 70px;
}
.content.rental__history__detail .battery .info__wrap > div ul li dl:last-of-type dd span {
  margin-left: 8px;
}
.content.rental__history__detail .battery .info__wrap > div ul li dl {
  display: flex;
  align-items: center;
}
.content.rental__history__detail .battery .info__wrap > div ul li dl dt {
  margin-right: 16px;
}
.content.rental__history__detail .battery .info__wrap > div ul li dl dd {
  display: flex;
  align-items: center;
}
.content.rental__history__detail .error__info .list__wrap > ul {
  display: flex;
  justify-content: space-between;
  margin-bottom: 16px;
}
.content.rental__history__detail .error__info .list__wrap > ul > li {
  width: 49%;
}
.content.rental__history__detail .error__info .list__wrap dl {
  display: flex;
  align-items: center;
  margin-bottom: 16px;
}
.content.rental__history__detail .error__info .list__wrap dl:last-of-type {
  margin-bottom: 0px;
}
.content.rental__history__detail .error__info .list__wrap textarea {
  min-height: 96px;
}
.content.rental__history__detail .error__info .list__wrap dl dd {
  width: 100%;
}
.content.rental__history__detail .error__info .list__wrap dl input {
  width: 100%;
}
.content.rental__history__detail .error__info .list__wrap dl dt {
  margin-right: 16px;
  white-space: nowrap;
}
@media (max-width: 900px) {
  .content.rental__history__detail .battery .info__wrap > div {
    flex-direction: column;
  }
  .content.rental__history__detail .battery .info__wrap > div > p {
    margin-right: 0px;
    margin-bottom: 12px;
    width: 100%;
    min-height: auto;
    height: 40px;
  }
  .content.rental__history__detail .user__certification .section__inner ul {
    flex-direction: column;
  }

  .content.rental__history__detail .user__certification.station .section__inner ul > li,
  .content.rental__history__detail .user__certification .section__inner ul > li {
    margin-bottom: 18px;
    width: 100%;
  }

  .content.rental__history__detail .user__certification .section__inner ul > li:last-of-type {
    margin-bottom: 0px;
  }
  .content.rental__history__detail .battery .info__wrap > div ul > li > p {
    min-width: 30px;
    margin-right: 8px;
  }
  .content.rental__history__detail .battery .info__wrap > div ul li dl {
    margin-right: 12px;
  }
}
@media (max-width: 700px) {
  .content.rental__history__detail .battery .info__wrap > div ul li dl dt {
    white-space: nowrap;
  }
  .content.rental__history__detail .battery .info__wrap > div ul li dl:nth-of-type(2) dd input {
    min-width: 190px;
  }
  .content.rental__history__detail .battery .info__wrap > div ul {
    overflow-x: auto;
  }
  .content.rental__history__detail .battery .info__wrap > div ul li dl:first-of-type dd input,
  .content.rental__history__detail .battery .info__wrap > div ul li dl:last-of-type dd input {
    min-width: 70px;
  }
  .content.rental__history__detail .id__wrap {
    grid-column: span 2 / span 2;
  }
}

@media (max-width: 600px) {
  .content.rental__history__detail .user__certification .section__inner ul dl {
    flex-direction: column;
    align-items: flex-start;
  }
  .content.rental__history__detail .user__certification .section__inner ul dl dt {
    margin-bottom: 8px;
  }
  .content.rental__history__detail .error__info .list__wrap > ul {
    flex-direction: column;
  }
  .content.rental__history__detail .error__info .list__wrap > ul > li {
    width: 100%;
    margin-bottom: 16px;
  }
  .content.rental__history__detail .error__info .list__wrap dl {
    flex-direction: column;
    align-items: flex-start;
  }
  .content.rental__history__detail .error__info .list__wrap dl dt {
    margin-bottom: 8px;
  }
  .content.rental__history__detail .error__info .list__wrap > ul > li:last-of-type {
    margin-bottom: 0px;
  }
}
